import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  formLogin;
  getCadastro;

  constructor(private fb: FormBuilder,private router: Router) { }

  ngOnInit()
  {
    this.formLogin = this.fb.group(
    {
      email: [''],
      cpf: ['']
    });
  }

  login()
  {
    this.getCadastro = JSON.parse(localStorage.getItem('cadastro'));
    //email
    const emailDB = this.getCadastro['email'], emailL = this.formLogin.get('email').value;
    //cpf
    const cpfDB = this.getCadastro['cpf'], cpfL = this.formLogin.get('cpf').value;
    if (emailDB === emailL && cpfDB === cpfL)//sucesso
    {
      localStorage.setItem('aprovado', 'Unique');
      this.router.navigate(['home-logada']);
    }
    else //erro
    {
      (<HTMLInputElement>document.getElementById("EMAIL")).className = "form-control login shadow-none err";
      (<HTMLInputElement>document.getElementById("CPF")).className = "form-control login shadow-none err";
      setTimeout(() =>
      {
        (<HTMLInputElement>document.getElementById("EMAIL")).className = "form-control login shadow-none";
        (<HTMLInputElement>document.getElementById("CPF")).className = "form-control login shadow-none";
      },1500)
    }
  }
}