import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-cadastro-cliente',
  templateUrl: './cadastro-cliente.component.html',
  styleUrls: ['./cadastro-cliente.component.css']
})
export class CadastroClienteComponent implements OnInit {

  formCadastro: any;
  valoresForm: any;
  conversao: any;
  valido: boolean = false;

  constructor(private fb: FormBuilder,private router: Router) { }

  ngOnInit()
  { 
    this.formCadastro = this.fb.group(
    {
      nome: [''],
      cpf: [''],
      email: [''],
      telefone: [''],
      endereco: ['']
    });
    this.formCadastro.valueChanges.pipe().subscribe(res => 
    {
      //console.log(res);
      this.valoresForm = res;
    });
  }

  cpfCheck(trial:any)
  {
      let A = trial;
      let BKP = "";
      let B = true;
      let C = true;
      let D = true;
      let E = "";

      A = A.replace(/[^\d]+/g,'');

      if(A==0o0||A==111111111||A==222222222||A==333333333||A==444444444||A==555555555||A==666666666||A==777777777||A==888888888||A==99999999999)
      {
        B=false;
      }
      else if(A==0o0||A==1111111111||A==2222222222||A==3333333333||A==4444444444||A==5555555555||A==6666666666||A==7777777777||A==8888888888||A==999999999999)
      {
        C=false;
      }
      else if(A==0o0||A==11111111111||A==22222222222||A==33333333333||A==44444444444||A==55555555555||A==66666666666||A==77777777777||A==88888888888||A==9999999999999)
      {
        D=false;
      }
      if(B||C||D)
      {
        //alert("Realizando calculo")
        let c1,c2,c3,c4,c5,c6,c7,c8,c9,c10,c11,s1,s2;
        c1 = parseInt(A.charAt(0));
        c2 = parseInt(A.charAt(1));
        c3 = parseInt(A.charAt(2));
        c4 = parseInt(A.charAt(3));
        c5 = parseInt(A.charAt(4));
        c6 = parseInt(A.charAt(5));
        c7 = parseInt(A.charAt(6));
        c8 = parseInt(A.charAt(7));
        c9 = parseInt(A.charAt(8));
        s1 = ((c1*10)+(c2*9)+(c3*8)+(c4*7)+(c5*6)+(c6*5)+(c7*4)+(c8*3)+(c9*2))*10;
        c10 = s1%11;
        if(c10==10){c10=0;}
        s2 = ((c1*11)+(c2*10)+(c3*9)+(c4*8)+(c5*7)+(c6*6)+(c7*5)+(c8*4)+(c9*3)+(c10*2))*10;
        if(c11==10){c11=0;}
        c11 = (s2%11);
        BKP = (c1.toString()+c2.toString()+c3.toString()+c4.toString()+c5.toString()+c6.toString()+c7.toString()+c8.toString()+c9.toString()+c10.toString()+c11.toString())
        E = (c1.toString()+c2.toString()+c3.toString()+"."+c4.toString()+c5.toString()+c6.toString()+"."+c7.toString()+c8.toString()+c9.toString()+"-"+c10.toString()+c11.toString())
      }
      if(A === BKP)
      {
        return true;
      }
      else
      {
        return false;
      }
  }

  emailCheck(trial:any)
  {
    let c1,c2,c3,c4,i,teste,resul;

    teste = trial.replace(/.br| /g, "")
    i = teste.length;
    c1 = teste.charAt(i-4);
    c2 = teste.charAt(i-3);
    c3 = teste.charAt(i-2);
    c4 = teste.charAt(i-1);
    resul = (c1+c2+c3+c4)

    
    if(resul == ".com" || resul ==  ".net" || resul ==  ".org"|| resul ==  ".gov" || resul ==  ".info")
    {
      return true;
    }
    else
    {
      return false;
    }
  }

  formCheck()
  {
    let nome = (<HTMLInputElement>document.getElementById("NOME")).value,
    cpf = (<HTMLInputElement>document.getElementById("CPF")).value,
    email = (<HTMLInputElement>document.getElementById("EMAIL")).value,
    telefone = (<HTMLInputElement>document.getElementById("TEL")).value,
    endereço = (<HTMLInputElement>document.getElementById("ENDE")).value,
    passe = 0;
    
    if(nome.length < 3)
    {
      passe += 1;
      (<HTMLInputElement>document.getElementById("NOME")).className = "form-control cadastro shadow-none err";
      setTimeout(() => {(<HTMLInputElement>document.getElementById("NOME")).className = "form-control cadastro shadow-none";}, 1500);
    }
    if(!this.cpfCheck(cpf))
    {
      passe += 1;
      (<HTMLInputElement>document.getElementById("CPF")).className = "form-control cadastro shadow-none err";
      setTimeout(() => {(<HTMLInputElement>document.getElementById("CPF")).className = "form-control cadastro shadow-none";}, 1500);
    }
    if(!this.emailCheck(email))
    {
      passe += 1;
      (<HTMLInputElement>document.getElementById("EMAIL")).className = "form-control cadastro shadow-none err";
      setTimeout(() => {(<HTMLInputElement>document.getElementById("EMAIL")).className = "form-control cadastro shadow-none";}, 1500);
    }
    if(telefone.length <= 10)
    {
      passe += 1;
      (<HTMLInputElement>document.getElementById("TEL")).className = "form-control cadastro shadow-none err";
      setTimeout(() => {(<HTMLInputElement>document.getElementById("TEL")).className = "form-control cadastro shadow-none";}, 1500);
    }
    if(endereço.length <= 5)
    {
      passe += 1;
      (<HTMLInputElement>document.getElementById("ENDE")).className = "form-control cadastro shadow-none err";
      setTimeout(() => {(<HTMLInputElement>document.getElementById("ENDE")).className = "form-control cadastro shadow-none";}, 1500);
    }
    if(passe == 0)
    {
      this.valido = true;
    }
  }

  cadastro()
  {
    this.formCheck();
    if(this.valido)
    {
      this.conversao = JSON.stringify(this.valoresForm);
      localStorage.setItem('cadastro', this.conversao);
      localStorage.setItem('aprovado', 'Unique');
      this.router.navigate(['cadastro-completo']);
    }
    else
    {
      //console.error("invalido");
    }
  }

    
}
