import { Component, OnInit, ElementRef } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  formLogin: any;
  getCadastro: any;
  Changer = localStorage.getItem('aprovado');
  public isCollapsed = true;

  constructor(private fb: FormBuilder,private router: Router, private elementRef:ElementRef) { }

  home() { this.router.navigate(['']); }
  cad_homeL()
  {
    if(this.Changer == "Unique")
    {
      this.router.navigate(['home-logada']);
    }
    else
    {
      this.router.navigate(['cadastro']);
    }
  }

  ngAfterViewInit()
  {
    window.addEventListener("resize", () => { this.SearchBerror() });
  }

  ngOnInit()
  {                                                                                                                                                                                                                                                                                                     console.info("Este Projeto foi concluido por Weider T.");
    this.formLogin = this.fb.group(
    {
      email: [''],
      cpf: ['']
    });
  }

  SearchBerror()
  {
    if(!this.isCollapsed && window.innerWidth > window.innerHeight/1.45)
    {
      this.isCollapsed = true;
      (<HTMLInputElement>document.getElementById("pesquisa")).className = "col-4";
    }
    else if(this.isCollapsed && window.innerWidth > window.innerHeight/1.45)
    {
      this.isCollapsed = true;
      (<HTMLInputElement>document.getElementById("pesquisa")).className = "col-4";
    }
    else 
    {
      this.isCollapsed = !this.isCollapsed;
      this.SearchB();
    }
    clearTimeout;
  }
  SearchB()
  {
    this.isCollapsed = !this.isCollapsed;
    if(this.isCollapsed)
    {
      (<HTMLInputElement>document.getElementById("pesquisa")).className = "col-4"
    }
    else
    {
      (<HTMLInputElement>document.getElementById("pesquisa")).className = "col-12"
    }
  }
  Finder()
  {
    this.getCadastro = JSON.parse(localStorage.getItem('cadastro'));
    //email
    const emailDB = this.getCadastro['email'], emailL = this.formLogin.get('email').value;
    //cpf
    const cpfDB = this.getCadastro['cpf'], cpfL = this.formLogin.get('cpf').value;
    if (emailDB === emailL && cpfDB === cpfL)//sucesso
    {
      localStorage.setItem('aprovado', 'Unique');
      this.router.navigate(['home-logada']);
    }
    else //erro
    {
      (<HTMLInputElement>document.getElementById("HEMAIL")).className = "form-control rounded-0 shadow-none err E-err";
      (<HTMLInputElement>document.getElementById("HCPF")).className = "form-control rounded-0 shadow-none err D-err";
      setTimeout(() =>
      {
        (<HTMLInputElement>document.getElementById("HEMAIL")).className = "form-control rounded-0 shadow-none border-0";
      (<HTMLInputElement>document.getElementById("HCPF")).className = "form-control rounded-0 shadow-none border-0";
      },1500)
    }
  }
}
