import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home-logada',
  templateUrl: './home-logada.component.html',
  styleUrls: ['./home-logada.component.css']
})
export class HomeLogadaComponent implements OnInit {

  Usuario = JSON.parse(localStorage.getItem('cadastro'));
  public Nome = this.Usuario.nome;

  Acesso = localStorage.getItem('aprovado');
  private codekey =  this.Acesso;

  
  constructor(private router: Router) { }
  
  ngOnInit()
  {
    if(this.codekey != 'Unique')
    {
      this.router.navigate(['login']);
    }
  }

}
