import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-cadastro-concluido',
  templateUrl: './cadastro-concluido.component.html',
  styleUrls: ['./cadastro-concluido.component.css']
})
export class CadastroConcluidoComponent implements OnInit {

  constructor(private router: Router) { }
  Contador = 10;

  ngOnInit()
  {
    this.Contando();
    console.log(1);
  }

  navegarParaLogin()
  {
    this.router.navigate(['login']);
  }
  
  temporizador():void
  {
    let Contador = this.Contador;
    (<HTMLInputElement>document.getElementById("tempo")).innerHTML = `${Contador} segundos`;
    if(Contador == 1)
    {
      (<HTMLInputElement>document.getElementById("tempo")).innerHTML = `${Contador} segundo`;
    }
    this.Contador -= 1;
    console.log(Contador)
  }

  Contando():void
  {
    setInterval(() =>
    {
      this.temporizador()
      if(this.Contador == -1)
      {
        this.navegarParaLogin();
      }
    },1000);
  }
}
